<template>
  <el-dialog
      :title="$t('user.nicknameDialogModify.title')"
      :visible.sync="dialogVisible"
      :close-on-click-modal=false
      :close-on-press-escape=false
      :before-close="handleCloseDialog"
      width="490px" class="nickname-modify-dialog">
      <el-form :model="ruleForm" :rules="ruleFormRules" ref="ruleForm" label-width="130px">
        <el-form-item :label="$t('form.nickname')" prop="nickname">
          <el-input v-model.trim="ruleForm.nickname" :placeholder="$t('form.nicknamePlaceholder')" maxlength="10"></el-input>
        </el-form-item>
        <div class="btn-group appeal-btn">
          <el-button type="primary"  @click="formSubmit('ruleForm')">{{ $t('dialog.sure') }}</el-button>
        </div>
      </el-form>
    </el-dialog>
</template>

<script>
  import { editNickname } from '@/api/user'

  export default {
    props: ['dialogVisible'],
    data() {
      return {
        ruleForm: {
          nickname: ''
        },
        ruleFormRules: {
          nickname: [
            { required: true, message: this.$t('errorTips.noNickname'), trigger: 'blur' }
          ]
        }
      }
    },
    methods: {
      handleCloseDialog() {
        this.$emit('closeDialog', 'nickNameModifyDialogVisible')
      },
      formSubmit(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            const data = { ...this.ruleForm }
            editNickname(data).then(res => {
              console.log('res', res)
              if (res.status !== 1000) {
                this.$message.error(res.msg)
                return false
              }
              this.$message.success(this.$t('errorTips.nicknameSuccess'))
              this.handleCloseDialog()
              this.$emit('getUserInfo')
            }).catch(() => {
            })
          } else {
            console.log('error submit!!')
            return false
          }
        })
      }
    }
  }
</script>

<style lang="less" scoped>
  .nickname-modify-dialog{
    .payway{
      margin-bottom: 15px;
      label{
        width: 86px;
        display: inline-block;
        text-align: right;
        padding-right: 10px;
      }
    }
    .code-tips{
      margin-top: 89px;
      color: #b4b6be;
      font-size: 12px;
    }
  }
</style>
<style lang="less">
  .nickname-modify-dialog{
    .avatar-uploader{
      width: 109px;
      display: inline-block;
    }
    .avatar-uploader .el-upload {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      position: relative;
      overflow: hidden;
    }
    .avatar-uploader .el-upload:hover {
      border-color: #409EFF;
    }
    .avatar-uploader-icon {
      font-size: 28px;
      color: #8c939d;
      width: 109px;
      height: 109px;
      line-height: 109px;
      text-align: center;
    }
    .avatar {
      width: 109px;
      height: 109px;
      display: block;
    }
    .el-button--primary{
      margin-left: 130px;
      margin-top: 15px;
    }
  }
</style>


